import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { changeCommentAndRiskLevelThunk, getComplitedInfoCompany, GetComplitedInfoCompanyResponseType } from "./complitedThunks";
import { RiskType } from "./supplierSlice";


export type QuestionsTypeRadioMultiTextCheckbox = 'checkbox' | 'radiobutton' | 'text' | 'multiline'
export type QuestionsComplitedSliceType = {
        "seq_no": number,
        "type": QuestionsTypeRadioMultiTextCheckbox,
        "is_required": boolean,
        "question": string,
        "options": Array<string>,
        "answer": string | null,
        "answers": Array<string>,
        "created_at": string | null,
        "risks": Array<string>
}
export type ComplitedSliceType = {
    companyId: string,
    company: string,
    createdAt: string,
    filledAt: string | null,
    checkedAt: string | null,
    riskLevel: RiskType,
    comment: string,
    ticket: string,

    parts: { [part: number]: Array<QuestionsComplitedSliceType> }
    isLoading: boolean,
    errors: {
        deleteError: string,
        onRequestDataError: string
    }
}
const initData:ComplitedSliceType = {
    companyId: '',
    company: '',
    createdAt: '',
    filledAt: null,
    checkedAt: null,
    riskLevel: null,
    comment: '',
    ticket: '',
    parts: { 1: [{
        "seq_no": 1,
        "type": 'checkbox',
        "is_required": true,
        "question": '',
        "options": [],
        "answer": null,
        "answers": [],
        "created_at": null,
        "risks": []
    }] },

    isLoading: false,
    errors: {
        deleteError: '',
        onRequestDataError: ''
    }
}

export const complitedSlice = createSlice({
    name: 'complited',
    initialState: initData,
    reducers: {
        deleteErrorOccuredAC: (state:ComplitedSliceType, action:PayloadAction<string>):ComplitedSliceType => {
            return {
                ...state,
                errors: {
                    ...state.errors,
                    deleteError: action.payload
                }
            }
        },
        changeErrorFromServerAC: (state:ComplitedSliceType, action:PayloadAction<string>):ComplitedSliceType => {
            return {
                ...state,
                errors: {
                    ...state.errors,
                    onRequestDataError: action.payload
                }
            }
        },
    },
    extraReducers: (builder) => {
        builder.addCase(getComplitedInfoCompany.pending, (state:ComplitedSliceType) => { state.isLoading = true })
        builder.addCase(getComplitedInfoCompany.fulfilled, (state:ComplitedSliceType, action: PayloadAction<GetComplitedInfoCompanyResponseType>) => { 
            state.companyId = action.payload.questionary.id;
            state.company = action.payload.questionary.company;
            state.createdAt = action.payload.questionary.created_at;
            state.checkedAt = action.payload.questionary.checked_at;
            state.filledAt = action.payload.questionary.filled_at;
            state.riskLevel = action.payload.questionary.risk_level;
            state.ticket = action.payload.questionary.ticket;
            state.comment = action.payload.questionary.comment;

            state.parts = {...action.payload.parts};

            state.isLoading = false;
        })
        builder.addCase(getComplitedInfoCompany.rejected, (state:ComplitedSliceType) => { state.isLoading = false })

        builder.addCase(changeCommentAndRiskLevelThunk.pending, (state:ComplitedSliceType) => {state.isLoading = true})
        builder.addCase(changeCommentAndRiskLevelThunk.fulfilled, (state:ComplitedSliceType, action: PayloadAction<GetComplitedInfoCompanyResponseType>) => {
            state.companyId = action.payload.questionary.id;
            state.company = action.payload.questionary.company;
            state.createdAt = action.payload.questionary.created_at;
            state.checkedAt = action.payload.questionary.checked_at;
            state.filledAt = action.payload.questionary.filled_at;
            state.riskLevel = action.payload.questionary.risk_level;
            state.ticket = action.payload.questionary.ticket;
            state.comment = action.payload.questionary.comment;

            state.isLoading = false;
        })
        builder.addCase(changeCommentAndRiskLevelThunk.rejected, (state:ComplitedSliceType) => {state.isLoading = false})
    }
})

export const {deleteErrorOccuredAC, changeErrorFromServerAC} = complitedSlice.actions;

export default complitedSlice.reducer;