import { createSlice, PayloadAction } from "@reduxjs/toolkit"

export type ConnectionErrorSliceType = {
    status: number,
    errorText: string
}
const connErrorInitData: ConnectionErrorSliceType = {
    status: 0,
    errorText: 'Connection Error. \nSomething went wrong with connection to the server!'
}
export const connectionErrorSlice = createSlice({
    name: 'connectionError',
    initialState: connErrorInitData,
    reducers: {
        changeErrorText: (state: ConnectionErrorSliceType, action: PayloadAction<string>) => {
            state.errorText = action.payload;
        }
    }
})


export const {changeErrorText } = connectionErrorSlice.actions;


export default connectionErrorSlice.reducer;
