import { createAsyncThunk } from "@reduxjs/toolkit";
import { complitedAPI } from "../../components/api/api";
import { changeErrorFromServerAC, QuestionsComplitedSliceType } from "./complitedSlice";
import { RiskType } from "./supplierSlice";


export type GetComplitedInfoCompanyResponseType = {
    questionary: {
        id: string,
        company: string,
        created_at: string,
        filled_at: string | null,
        checked_at: string | null,
        risk_level: RiskType,
        ticket: string,
        comment: string
    },
    parts: { [part: number]: Array<QuestionsComplitedSliceType> }
}
export type GetComplitedInfoCompanyRequestType = {
    questionaryId: string,
    t: any
}
export const getComplitedInfoCompany = createAsyncThunk(
    'complited/getComplitedInfoCompany',
    async (dataToSend: GetComplitedInfoCompanyRequestType, {rejectWithValue, dispatch}) => {
        try {
            const res = await complitedAPI.getInfoCompany(dataToSend.questionaryId);
            return res.data.data;
        } catch (err: any) {
            if (err.response.data.message.include("JWT")) {
                dispatch(changeErrorFromServerAC( dataToSend.t("complited_something_with_JWT_token") ));
            } else {
                dispatch(changeErrorFromServerAC( dataToSend.t("complited_server_error") ));
            }
        }
    }
);

export type OnRiskAndCommentChangeType = {
    risk_level: RiskType,
    comment: string,
    t: any
}
export const changeCommentAndRiskLevelThunk = createAsyncThunk(
    'complited/changeCommentAndRiskLevel',
    async (dataToSend:OnRiskAndCommentChangeType, {rejectWithValue, dispatch}) => {
        try {
            const res = await complitedAPI.changeCommentAndRisk(dataToSend);
            return res.data.data;
        } catch (err: any) {
            if (err.response.data.message.include("JWT")) {
                dispatch(changeErrorFromServerAC( dataToSend.t("complited_something_with_JWT_token") ));
            } else {
                dispatch(changeErrorFromServerAC( dataToSend.t("complited_server_error") ));
            }
        }
        
    }
);